export const SSI = () => {
    return (
      <div id="ssi" className="ssi-container">
        <div className="main-container">
          <div className="section">
            <h1>
              Support et mise  disposition de services
              informatiques - (E5)
            </h1>
            <h2>1  OBJECTIF</h2>
            <p>
              Cette preuve vise  valuer chez la personne candidate l'acquisition des comptences dcrites dans le bloc de comptences 
               Support et mise  disposition de services informatiques ,  savoir :
            </p>
            <ul>
              <li>Grer le patrimoine informatique</li>
              <li>Rpondre aux incidents et demandes d'assistance et d'volution</li>
              <li>Dvelopper la prsence en ligne de l'organisation</li>
              <li>Travailler en mode projet</li>
              <li>Mettre  disposition des utilisateurs un service informatique</li>
              <li>Organiser son dveloppement professionnel</li>
            </ul>
          </div>
  
          <div className="section">
            <h2>2  MODALITS D'VALUATION</h2>
            
            <div className="content-wrapper">
              <div className="sub-section">
                <h3>Forme ponctuelle orale (dure 40 minutes) :</h3>
                <p>
                  L'preuve comporte deux phases conscutives. Pendant 10 minutes (maximum) la personne candidate prsente son 
                  parcours de professionnalisation et justifie les modalits d'acquisition des comptences du bloc 
                   Support et mise  disposition de services informatiques . 
                </p>
                <p>
                  S'en suit 30 minutes d'change avec le jury qui permettent, d'une part, d'expliciter et d'apprcier 
                  le parcours de professionnalisation et, d'autre part, d'approfondir une ou plusieurs ralisations 
                  dcrites dans le dossier numrique. 
                </p>
                <p>
                  L'entretien permet d'apprcier la capacit de la personne candidate  mobiliser les comptences vises, 
                  sa capacit  rendre compte d'un travail ralis au sein d'une quipe projet en mettant clairement 
                  en vidence sa contribution personnelle.
                </p>
              </div>
  
              <div className="sub-section">
                <h3>Contrle en cours de formation :</h3>
                <p>
                  L'preuve est organise de la mme faon que dans la forme ponctuelle.  l'issue des valuations, 
                  l'quipe pdagogique de l'tablissement de formation adresse au jury, pour chaque personne candidate, 
                  la proposition de note sur 20 points accompagne de la grille d'aide  l'valuation renseigne 
                  (dont le modle est fourni par la circulaire d'organisation de l'examen). 
                </p>
                <p>
                  Conformment  la rglementation, le jury peut demander  avoir communication des dossiers d'valuation 
                  des candidats. Ces documents sont tenus  la disposition du jury et de l'autorit rectorale pour la 
                  session considre jusqu' la session suivante.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }