import music from "../assets/img/music.png";
import sportshop from "../assets/img/sportshop.png";
import { Row, Tab, Nav } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";
import "animate.css";
import TrackVisibility from "react-on-screen";
import sportshoppdf from "../assets/doc/sportshop.pdf";
import attestSportShop from "../assets/doc/Fiche E6-Situation2-Sportshop - Yousra ZAABAT.pdf";
import musicpdf from "../assets/doc/music.pdf";
import attestMusic from "../assets/doc/Fiche E6-Situation1-MusicLibrary - Yousra ZAABAT.pdf";
import colorSharp2 from "../assets/img/color-sharp2.png";

export const CDA = () => {
  const situations1 = [
    {
      title: "MusicLibrary",
      description: "Design & Development",
      imgUrl: music,
      pdfFile: musicpdf,
      attest: attestMusic,
    },
  ];
  const situations2 = [
    {
      title: "SportShop",
      description: "Design & Development",
      imgUrl: sportshop,
      pdfFile: sportshoppdf,
      attest: attestSportShop,
    },
  ];
  return (
    <section className="cda-container" id="cda">
      <div className="main-container">
        <TrackVisibility>
          {({ isVisible }) => (
            <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
              <div className="section">
                <h1>
                  Conception et dveloppement
                  d'applications - (E6)
                </h1>
                <h2>1  OBJECTIF</h2>
                <p>
                  Cette preuve vise  valuer les comptences acquises par le
                  candidat dans le domaine de la conception et du dveloppement
                  d'applications, correspondant  l'option B "Solutions
                  Logicielles et Applications Mtiers" (SLAM) du BTS Services
                  Informatiques aux Organisations.
                </p>
                <p>L'preuve permet d'valuer la capacit du candidat  :</p>
                <ul>
                  <li>Concevoir et dvelopper une solution applicative</li> 
                  <li>
                    Utiliser diffrents langages de programmation et frameworks
                  </li>
                  <li>
                    Assurer la maintenance corrective et volutive d'une solution
                    applicative
                  </li>
                  <li>
                    Grer des bases de donnes relationnelles ou non
                    relationnelles
                  </li>
                  <li>
                    Utiliser des outils de dveloppement et de gestion de versions
                  </li>
                  <li>
                    Assurer la cyberscurit d'une solution applicative et de son
                    dveloppement
                  </li>
                </ul>
              </div>

              <div className="section">
                <h1>2  MODALITS D'VALUATION</h1>

                <div className="content-wrapper">
                  <div className="sub-section">
                    <h2>Forme de l'preuve</h2>
                    <p>
                      L'preuve E6 "Parcours de professionnalisation" est une
                      preuve pratique et orale qui se dcompose en deux parties :
                    </p>
                    <ul>
                      <li>
                        Une premire partie centre sur le parcours de
                        professionnalisation du candidat
                      </li>
                      <li>
                        Une seconde partie portant sur les comptences relevant du
                        processus de gestion du patrimoine informatique
                      </li>
                    </ul>
                    <p>
                      Le candidat s'appuie sur un dossier comprenant un tableau de
                      synthse associ  son portefeuille de comptences
                      professionnelles, les attestations de stage ou certificats de
                      travail, et une attestation de la ralit des activits et
                      missions conduites.
                    </p>
                  </div>

                  <div className="sub-section">
                    <h2>
                      Droulement de l'preuve (40 minutes + 1h30 de prparation)
                    </h2>
                    <p>
                      L'preuve dure 40 minutes et est prcde d'une priode de
                      prparation de 1h30. Elle se droule en deux phases :
                    </p>
                    <ul>
                      <li>
                        <span className="highlight">
                          Prsentation du parcours de professionnalisation :
                        </span>{" "}
                        Le candidat prsente son parcours et les comptences
                        acquises
                      </li>
                      <li>
                        <span className="highlight">
                          Entretien sur les comptences :
                        </span>{" "}
                        Les examinateurs interrogent le candidat sur les comptences
                        relevant de la conception et du dveloppement d'applications
                      </li>
                    </ul>
                  </div>
                </div>

                <div className="content-wrapper">
                  <div className="sub-section">
                    <h2>valuation</h2>
                    <p>
                      La commission d'interrogation est compose de deux membres :
                    </p>
                    <ul>
                      <li>
                        Un professeur charg des enseignements professionnels en STS
                        services informatiques aux organisations, dans le domaine de
                        spcialit du candidat
                      </li>
                      <li>
                        Un professionnel du secteur d'activits des prestations
                        informatiques ou,  dfaut, le professeur charg de
                        l'enseignement d'analyse conomique, managriale et
                        juridique des services informatiques
                      </li>
                    </ul>
                    <p>L'valuation porte notamment sur :</p>
                    <ul>
                      <li>La qualit de la prsentation</li>
                      <li>
                        La pertinence de l'analyse critique du parcours de
                        professionnalisation
                      </li>
                      <li>
                        La varit des situations professionnelles rencontres
                      </li>
                      <li>
                        Le degr de matrise des comptences relevant de la
                        participation  un projet et de l'accompagnement de la mise
                        en place d'un nouveau service
                      </li>
                      <li>
                        La pertinence d'usage de la gestion des configurations
                      </li>
                      <li>
                        L'efficacit de l'organisation de la veille technologique
                      </li>
                    </ul>
                  </div>

                  <div className="sub-section">
                    <h2>Contrle en cours de formation</h2>
                    <p>
                      Pour les candidats en formation initiale dans un tablissement
                      public ou priv sous contrat, en formation continue dans un
                      tablissement public habilit, ou en apprentissage dans un CFA
                      habilit, l'preuve peut tre organise en contrle en cours
                      de formation (CCF).
                    </p>
                    <p>
                      L'valuation est alors organise par l'tablissement selon des
                      modalits similaires  l'preuve ponctuelle, et doit avoir
                      lieu avant une date fixe par les autorits acadmiques.
                    </p>
                  </div>
                </div>
              </div>

              <Tab.Container id="projects-tabs" defaultActiveKey="first">
                <Nav
                  variant="pills"
                  className="nav-pills mb-5 justify-content-center align-items-center"
                  id="pills-tab"
                >
                  <Nav.Item>
                    <Nav.Link eventKey="first">Situation 1</Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="second">Situation 2</Nav.Link>
                  </Nav.Item>
                </Nav>
                <Tab.Content
                  id="slideInUp"
                  className={
                    isVisible ? "animate__animated animate__slideInUp" : ""
                  }
                >
                  <Tab.Pane eventKey="first">
                    <Row className="rowie">
                      {situations1.map((situation1, index) => {
                        return <ProjectCard key={index} {...situation1} />;
                      })}
                    </Row>
                  </Tab.Pane>
                  <Tab.Pane eventKey="second">
                    <Row className="rowie">
                      {situations2.map((situation2, index) => {
                        return <ProjectCard key={index} {...situation2} />;
                      })}
                    </Row>
                  </Tab.Pane>
                </Tab.Content>
              </Tab.Container>
            </div>
          )}
        </TrackVisibility>
      </div>
    </section>
  );
};